@keyframes watch {
  from {
    transform-origin: bottom;
    transform: translateY(-50%)rotate(0);
  }

  to {
    transform-origin: bottom;
    transform: translateY(-50%)rotate(360deg);
  }
}

.page {
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__minute {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 60s linear infinite watch;
  position: absolute;
  transform: translateY(-50%);
}

.stopwatch__second {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 3600s steps(60, end) infinite watch;
  position: absolute;
  transform: translateY(-50%);
}

.stopwatch__decorate {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch--speed-up .stopwatch__minute {
  animation: 600s steps(60, end) infinite watch;
}

.stopwatch--speed-up .stopwatch__second {
  animation: 10s linear infinite watch;
}
